.home {
    background-image: url("../images/myImage10.png");
    background-size: 45%;
    background-position: center top;
    background-repeat: no-repeat;
    height: 100%;
    margin: 0;
    margin-top: 20px;
    padding: 25px;
    overflow: auto;
    transition: all 3s;
    display: flex;
    align-items: flex-end; 
}
.sampleSites {
    width: 100%;
    height: 100%;
    text-align: center;
    margin: 20px;
    position: relative;
    padding: 25px;
    z-index: 600;
    overflow: scroll;
}
@media(min-height: 1024px) {
    .home {
        background-size: 75%;
    }
    .sampleSites {
        padding-bottom: 10% !important;
    }
}
.sampleSites>section {
    padding: 25px 10px;
    border-radius: 30px;
}
.sampleSites img:hover {
    cursor: pointer;
}
.temporaryNotice{
    position: absolute;
    background-color:rgba(196, 196, 18,.7);
    padding: 20px;
    border-radius: 15px ;
    top: 30px;
    left: 50%;
    transform: translate(-50%);
    width: 60%;
    z-index: 650;
}
.sampleSites h3{
    color:goldenrod;
    font-family:fantasy;
    font-size: clamp(1.5rem,2vw,2.5rem)!important;

}
.sites {
    width: 100%;
    border-radius: 20px;
    box-shadow: 0px 0px 10px antiquewhite;
}

